.comp-layout {
    background-color: $color__layout-background;
    color: $color__font-main;

    &.container {
        display: flex;
        flex-flow: column nowrap;
        min-height: 100vh;
        margin: 1rem;
    }

    &.body {
        min-height: 300px;
        display: flex;
        justify-content: center;
    }
}

@media only screen and (min-width: 1024px) {
    .comp-layout {
        &.container {
            width: 50%;
            margin: 1rem auto;
        }
    }
}
