/**
 * # COMPONENTS
 *
 */
body {
  background: #282c34; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1rem; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.h1 {
  font-size: 1.5rem; }

.App {
  text-align: center; }

.comp-layout {
  background-color: #282c34;
  color: #fff; }
  .comp-layout.container {
    display: flex;
    flex-flow: column nowrap;
    min-height: 100vh;
    margin: 1rem; }
  .comp-layout.body {
    min-height: 300px;
    display: flex;
    justify-content: center; }

@media only screen and (min-width: 1024px) {
  .comp-layout.container {
    width: 50%;
    margin: 1rem auto; } }

.component-button button {
  color: white; }

.App-header {
  height: 250px;
  font-size: calc(10px + 1vmin);
  color: #fff;
  margin: 1rem;
  position: relative; }
  .App-header .logo svg {
    max-height: 150px; }
  @media (prefers-reduced-motion: no-preference) {
    .App-header .logo {
      animation: App-logo-spin infinite 20s linear; } }
  .App-header .title {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0; }
  .App-header .toast-span {
    color: orange;
    text-decoration: underline;
    text-shadow: 0px 0px 8px #000000; }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

@media only screen and (min-width: 1024px) {
  .App-header {
    overflow: hidden; }
    .App-header .logo svg {
      max-height: none;
      width: 75%; } }

.forecast-icon .icon svg {
  fill: white;
  stroke: white; }

.searchForm .form-textfield,
.searchForm button {
  margin: 0.5rem; }

.searchForm .MuiFormLabel-root {
  color: white; }
  .searchForm .MuiFormLabel-root.Mui-focused {
    color: white; }

.searchForm .MuiButton-outlined {
  color: white;
  border-color: #60dbfc; }
  .searchForm .MuiButton-outlinedPrimary:hover {
    border-color: #60dbfc; }
  .searchForm .MuiButton-outlined .MuiInputBase:focus {
    color: white; }

.searchForm .MuiInputBase-root {
  color: white; }

.searchForm .MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline,
.searchForm .MuiOutlinedInput-notchedOutline {
  border-color: #60dbfc; }

.comp-weather.container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  min-height: 200px; }

.comp-weather.component {
  min-height: 200px;
  display: flex;
  justify-content: center;
  align-items: center; }

.comp-weather .MuiCircularProgress-root {
  margin: 0 auto; }

.comp-weather .app-instructions {
  color: #60dbfc; }

.comp-weather .temp-symbol {
  color: orange; }
